import React from "react";

const ModalCargaMasiva = ({ children, isOpen, closeModal }) => {
const stopModal = e => e.stopPropagation()

  const iconCerrar = (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width="25"
      height="25"
      fill="currentColor"
      className="bi bi-x-circle-fill"
      viewBox="0 0 16 16"
    >
      <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM5.354 4.646a.5.5 0 1 0-.708.708L7.293 8l-2.647 2.646a.5.5 0 0 0 .708.708L8 8.707l2.646 2.647a.5.5 0 0 0 .708-.708L8.707 8l2.647-2.646a.5.5 0 0 0-.708-.708L8 7.293 5.354 4.646z" />
    </svg>
  );

  return (
    <div className={`modal ${isOpen && "is-open"}`} onClick={closeModal}>
      <div className="modal-container2 modalcontainereditar" onClick={stopModal}>      
            <button className="modal-close iconCerrar" onClick={closeModal}>{iconCerrar}</button>
            {children}
            
      </div>
    </div>
  );
};

export default ModalCargaMasiva;
